




















import { Component, Vue } from 'vue-property-decorator';
import { mapGetters } from 'vuex';
import RootPanel from '../components/RootPanel.vue';

interface ActiveComponent {
   obj: object;
   pos: number[];
}

@Component({
  components: { RootPanel},
  computed: {...mapGetters(['getJSON', 'getComponent'])},
})

export default class MyCanvas extends Vue {

   private activeComponent: object = {
      obj: new Object(),
      pos: new Array(),
   };

   private isActive: boolean = true;

}
