















































import { Component, Vue } from 'vue-property-decorator';
import { mapGetters } from 'vuex';
import draggable from 'vuedraggable';

interface Directory {
   component_type: object;
}

@Component({
   components: {
      draggable,
   },
   computed: {
      ...mapGetters({
         list: 'getComponents',
         directory: 'getDirectory',
      }),
   },
})
export default class Accordion extends Vue {
   private list!: any[];
   private activePanel: number = 1;
   private directory!: Directory;
   private get componentsType(): object { return this.directory.component_type; }
   private get panel(): object[] {
      const array = this.list.filter(el => el.t === 19);
      return array;
   }
   private get components(): any[] {
      const array = this.list.filter(el => el.t !== 19);
      return array;
   }

}
