

























import { Component, Vue } from 'vue-property-decorator';
import { mapGetters } from 'vuex';
import draggable from 'vuedraggable';

interface Directory {
   component_type: object;
}

@Component({
   components: {
      draggable,
   },
   props: {
      item: {
         type: [Array, Object],
         required: true,
      },
      isActive: {
         type: Array,
         required: false,
      }
   },
   computed: {
      ...mapGetters({
         directory: 'getDirectory',
      }),
   },
})
export default class RootPanel extends Vue {
   private list!: any[];
   private isActive!: any[];
   private directory!: Directory;
   private get componentsType(): object { return this.directory.component_type; }
   private item!: any;

   private get classActive(): any {
      let res = null;
      if (this.isActive[0] === true) {
         const last = this.isActive[1].length - 1;
         res = this.isActive[1][last];
      }
      return res;
   }

   private get Model(): any[] {
      return [];
   }

   private set Model(v) {
      const item = v[0];
      this.pushNewItem(item);
   }
}
