












































import { Component, Vue } from 'vue-property-decorator';
import { mapMutations } from 'vuex';
import draggable from 'vuedraggable';
import Row from './Row.vue';
import Panel from './Panel.vue';

@Component({
   components: {
      draggable, Row, Panel
   },
   props: {
      list: {
         type: Array,
         required: true,
      },
      activeComponent: {
         type: Object,
         required: false,
      }
   },
   methods: {
      ...mapMutations(['pushNewItem', 'pushToPanel', 'fixEmptyRow']),
   },
})
export default class RootPanel extends Vue {

   private get Model(): any[] {
      return [];
   }

   private set Model(v) {
      const item = v[0];
      this.pushNewItem(item);
   }

   // private console(e: any): void {
   //    console.log(e);
   // }

   private active(e: number[], r: number): void {
      e.unshift(r);
      this.$emit('active', e)
   } 
}
